.fetch-incidents {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
}

.filter-form {
  height: 40%;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.filter--text {
  font-weight: bold;
  font-size: 12px;
  width: 90%;
}
.filter--bar {
  height: 40px;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.filter--bar-split {
  height: 40px;
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.form--input-split {
  width: 40%;
  height: 35px;
  background-color: #eef4d4;
  display: flex;
  align-items: center;
  font-family: "Montserrat";
  font-weight: bold;
  color: black;
  font-size: 12px;
  padding-left: 10px;
  border-radius: 2px;
  border: none;
}

.result-box {
  height: 55%;
  width: 90%;
  background-color: #eef4d4;
  display: flex;
  justify-content: center;
  align-items: center;
}
