.App {
  display: flex;
  width: 100%;
  height: 100vh;
  padding: 10px;
  background-color: #eef4d4;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: "Montserrat";
}

.app-heading {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  width: 100%;
  height: 10%;
  padding: 3px;
  padding-left: 30px;

}
.app-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90%;
}
.body--heading {
  width: 100%;
  height: 5%;
  padding: 3px;
  padding-left: 30px;
  font-size: 15px;
  font-weight: bold;
}
.body--main {
  width: 100%;
  height: 95%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.endpoint--card {
  background-color: #d9d9d9;
  width: 32%;
  height: 95%;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.ep-card--heading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
}

.method {
  font-size: 25px;
  font-weight: bold;
  color: green;
}
.route {
  margin-left: 5px;
  font-size: 15px;
  margin-top: 5px;
  font-weight: bold;
}
.ep-card--body {
  display: flex;
  width: 100%;
  height: 90%;
}
