.create-incident {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
}

.ci-form {
  height: 55%;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.ci--bar {
  height: 40px;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.form--input {
  width: 100%;
  height: 35px;
  background-color: #eef4d4;
  display: flex;
  align-items: center;
  font-family: "Montserrat";
  font-weight: bold;
  color: black;
  font-size: 12px;
  padding-left: 10px;
  border-radius: 2px;
  border: none;
}

.form--button {
  width: 100px;
  height: 35px;
  font-family: "Montserrat";
  font-weight: bold;
  font-size: 12px;
  border-radius: 3px;
  border: none;
  background-color: #136505;
  color: #eef4d4;
}

.form--button:hover {
  background-color: #eef4d4;
  color: #136505;
  border: 2px solid #136505;
  cursor: pointer;
}

.result-box--if {
  height: 45%;
  width: 90%;
  background-color: #eef4d4;
  display: flex;
  justify-content: center;
  align-items: center;
}

