.incident-country {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.filter-form--ic {
  height: 35%;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.result-box--ic {
  height: 60%;
  width: 90%;
  background-color: #eef4d4;
  display: flex;
  justify-content: center;
  align-items: center;
}

.result--empty {
  font-weight: bold;
  font-size: 15px;
}
